.cartpagebody{
    background-image:linear-gradient(90deg, rgba(247, 247, 220, 0.705),rgba(243, 192, 192, 0.674))
}
.cartpageheader, .iconcart{
    padding-left:1vw;
    font-family: 'Poppins', sans-serif;
    font-size: 3vmax;
    display: flex;
    align-items: center;
    gap: 1rem;
    border-bottom: 1px solid black;
}
.cartpagecontainer{
    min-height: 80vh;
}
.cartpagegrid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: 10px;
    grid-column-gap:10px;
    margin: 20px 0;
    padding-left: 1vw;
    padding-right: 1vw;
    min-height: auto;
}
.cartpageitemdiv{
    display: flex;
    flex-direction: column;
    gap:0.5rem;
    justify-content: space-between;
}
.cartpagecheckout{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 3vh;
    padding-left: 1vw;
    padding-right: 1vw;
}
.cartpagecheckoutprice{
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}

@media screen and (max-width: 768px) {
    .cartpagegrid{
        grid-template-columns: 1fr 1fr;
    }

}